<template>
    <div class="wh-fill">
        <b-container class="navigate-children-container">
            <b-row align-h="center">
                <b-col cols="12" class="border-radius6 padding-sm">
                    <div class="justify-center wh-fill flex padding-top-lg">
                        <div class="top-container border-top-groove border-radius3 shadow">

                            <div v-if="githubCallBackCode" class="text-center padding-top-xl">
                                <svg style="margin-top: 30px;" height="66" viewBox="0 0 16 16" version="1.1" width="66"
                                     aria-hidden="true">
                                    <path fill-rule="evenodd"
                                          d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.013 8.013 0 0 0 16 8c0-4.42-3.58-8-8-8z"></path>
                                </svg>
                                <!--loading-->
                                <div class="justify-center flex" style="margin-top: 140px;">
                                    <b-spinner
                                            variant="info"
                                            label="Spinning"
                                            style="width: 3rem;height: 3rem;display: block;">
                                    </b-spinner>
                                </div>
                                <p class="padding-top"><span
                                        class="text-color">Authorization success is landing...</span></p>

                            </div>

                            <div v-if="!githubCallBackCode" class="padding-sm text-center">
                                <svg style="margin-top: 30px;" height="66" viewBox="0 0 16 16" version="1.1" width="66"
                                     aria-hidden="true">
                                    <path fill-rule="evenodd"
                                          d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.013 8.013 0 0 0 16 8c0-4.42-3.58-8-8-8z"></path>
                                </svg>
                                <span class="margin-tb-lg text-xs"
                                      style="display: block; background-image: -webkit-gradient( linear, left 0, right 0, from(rgb(4, 94, 170)), to(rgb(1, 152, 216)) );-webkit-background-clip: text;-webkit-text-fill-color: transparent;">Log in using github</span>

                                <div class="justify-center flex" style="padding-top: 80px;cursor: pointer">
                                    <a :href="githubAuthUrl" style="text-decoration: none">
                                        <div
                                                class="bg-gradual-blue shadow-blur round text-center text-bold"
                                                style="width: 160px;height: 32px;line-height: 31px;font-size: 0.9em;">
                                            <span class="icon-safe lg text-white"></span>
                                            Authorized login
                                        </div>
                                    </a>
                                </div>
                                <p class="mt-3">
                                    New to GitHub?
                                    <a href="https://github.com/join?source=login">Create an account</a>
                                </p>
                            </div>
                        </div>

                    </div>

                </b-col>
            </b-row>
        </b-container>
    </div>

</template>

<script>

    import {getRandomLetter, checkToken} from "@/utils"

    export default {
        name: "Login",
        data() {
            return {
                githubAuthUrl: `https://github.com/login/oauth/authorize?client_id=${this.$http.githubClientId()}&state=${getRandomLetter(32)}`,
                githubCallBackCode: "",
                githubCallBackState: ""
            }
        },
        created() {
            this.githubCallBackCode = this.$route.query.code
            this.githubCallBackState = this.$route.query.state

            if (checkToken()) {
                this.$router.push({name: "Index"})
            } else {
                this.githubOauthLogin(this.githubCallBackCode, this.githubCallBackState)
            }
        },
        methods: {
            githubOauthLogin(code, state) {
                if (!code || !state) {
                    return
                }
                this.$http({
                    url: this.$http.baseUrl(`/oauth/github/login`),
                    method: "POST",
                    data: this.$http.postDataMerge({
                        code: code,
                        state: state
                    })
                }).then(({data}) => {
                    if (data && data.code === 0) {
                        this.$cookie.set('user', escape(JSON.stringify({
                            token: data.token,
                            id:data.id,
                            name: data.name,
                            avatar: data.avatar,
                            expire: new Date().getTime() + data.expire * 1000
                        })))
                        this.$router.push({name: 'index'})
                    } else {
                        return Promise.reject(data.msg)
                    }
                }).catch(msg => {
                    this.githubCallBackCode = ""
                    this.$bvToast.toast(msg.toString(), {
                        title: '登录失败：',
                        toaster: 'b-toaster-top-center',
                        autoHideDelay: 5000
                    })
                })
            }
        }
    }
</script>

<style scoped>
    .top-container {
        height: 460px;
        width: 390px;
        background: rgba(255, 255, 255, 0.8);
    }
</style>